<template>
  <div class="canvas-container">
    <canvas ref="canvas" width="200" height="200"></canvas>
  </div>
</template>

<script>
export default {
  mounted () {
    this.drawQ()
  },
  methods: {
    drawQ () {
      const canvas = this.$refs.canvas
      const ctx = canvas.getContext('2d')

      // Set styles for the shape
      ctx.fillStyle = '#333' // Color of the shape

      // Draw the shape
      ctx.beginPath()
      ctx.moveTo(50, 20) // Move to top-left point
      ctx.lineTo(150, 20) // Draw horizontal line to top-right point
      ctx.lineTo(150, 80) // Draw vertical line down to bottom-right point
      ctx.lineTo(100, 80) // Draw horizontal line to center-right point
      ctx.lineTo(100, 130) // Draw vertical line down to bottom-center point
      ctx.lineTo(50, 130) // Draw horizontal line to bottom-left point
      ctx.closePath() // Close the shape path

      ctx.fill() // Fill the shape with color
    }
  }
}
</script>

<style scoped>
.canvas-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; /* Adjust as needed */
}

canvas {
  border: 1px solid #ccc; /* Border for the canvas */
}
</style>
