<template>
  <div class="burning-icon">
    <i class="fas fa-fire fa-4x"></i></div>
</template>

<style>
.burning-icon {
  width: 200px;
  height: 200px;
  position: relative;
  overflow: hidden;
  border-radius: 50%; /* Add a circular border for a more flame-like shape */
}

.burning-icon i {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: orange; /* Set a fire-like color for the icon */
  animation: burning 2s infinite alternate ease-in-out;
}

@keyframes burning {
  0% {
    opacity: 0.6;
  }
  50% {
    opacity: 1;
    transform: translate(-50%, -20%) scale(1.2); /* Adjust movement for a flickering effect */
  }
  100% {
    opacity: 0;
    transform: translate(-50%, -100%) scale(1);
  }
}

</style>
